import request from '@/utils/request';

export function serversPower(username) {
  return request({
    url: '/api/user/serversPower',
    method: 'get',
    params: { username }
  });
}
export function viewsPower(username) {
  return request({
    url: '/api/user/viewsPower',
    method: 'get',
    params: { username }
  });
}
export function allUsersInfo() {
  return request({
    url: '/api/user/allUsersInfo',
    method: 'get'
  });
}

export function changeServerPower(server_power) {
  return request({
    url: '/api/user/changeServerPower',
    method: 'post',
    data: {
      server_power
    }
  });
}
export function changeViewPower(view_power) {
  return request({
    url: '/api/user/changeViewPower',
    method: 'post',
    data: {
      view_power
    }
  });
}

