<template>
  <div class="menu-wrapper">
    <template v-for="parent in routes">
      <el-submenu :index="parent.index" :key="parent.category" v-if="parent.childs.length>0">
        <template slot="title">
          <svg-icon v-if="parent.icon" :icon-class="parent.icon"></svg-icon>
          <span slot="title">{{parent.title}}</span>
        </template>
        <template v-for="child in parent.childs" v-if="parent.childs.length>0">
          <router-link :to="child.path" :key="child.id"> 
            <el-menu-item :index="child.path">
              <svg-icon v-if="child.icon" :icon-class="child.icon"></svg-icon>
              <span slot="title">{{child.title}}</span>
            </el-menu-item>
          </router-link>
        </template>
      </el-submenu>
    </template>
    <!-- <template v-for="item in routes" v-if="!item.hidden&&item.children">

      <router-link v-if="hasOneShowingChildren(item.children) && !item.children[0].children&&!item.alwaysShow" :to="item.path+'/'+item.children[0].path"
        :key="item.children[0].name">
        <el-menu-item :index="item.path+'/'+item.children[0].path" :class="{'submenu-title-noDropdown':!isNest}">
          <svg-icon v-if="item.children[0].meta&&item.children[0].meta.icon" :icon-class="item.children[0].meta.icon"></svg-icon>
          <span v-if="item.children[0].meta&&item.children[0].meta.title" slot="title">{{item.children[0].meta.title}}</span>
        </el-menu-item>
      </router-link>

      <el-submenu v-else :index="item.name||item.path" :key="item.name">
        <template slot="title">
          <svg-icon v-if="item.meta&&item.meta.icon" :icon-class="item.meta.icon"></svg-icon>
          <span v-if="item.meta&&item.meta.title" slot="title">{{item.meta.title}}</span>
        </template>

        <template v-for="child in item.children" v-if="!child.hidden">
          <sidebar-item :is-nest="true" class="nest-menu" v-if="child.children&&child.children.length>0" :routes="[child]" :key="child.path"></sidebar-item>

          <router-link v-else :to="item.path+'/'+child.path" :key="child.name">
            <el-menu-item :index="item.path+'/'+child.path">
              <svg-icon v-if="child.meta&&child.meta.icon" :icon-class="child.meta.icon"></svg-icon>
              <span v-if="child.meta&&child.meta.title" slot="title">{{child.meta.title}}</span>
            </el-menu-item>
          </router-link>
        </template>
      </el-submenu>

    </template> -->
  </div>
</template>

<script>
import { viewsPower } from '@/api/user';

export default {
  name: 'SidebarItem',
  data() {
    return {
      viewsList: [],
      routesArr: [
        { title: 'Mail', category: 1, index: '/mail', icon: 'example' },
        { title: 'Account', category: 2, index: '/account', icon: 'example' },
        { title: 'Activity', category: 3, index: '/activity', icon: 'example' },
        { title: 'Player', category: 5, index: '/player', icon: 'example' },
        { title: 'Settings', category: 4, index: '/settings', icon: 'example' }
      ],
      routes: []
    };
  },
  created() {
    this.getViewsPower(this.$store.getters.name);
  },
  methods: {
    getViewsPower(username) {
      viewsPower(username)
        .then(response => {
          this.viewsList = response.Data.views;
          this.routesArr.forEach(element => {
            var childs = this.viewsList.filter(
              p => parseInt(p.category) === element.category && parseInt(p.hidden) === 0
            );
            element.childs = childs;
          });
          this.routes = this.routesArr;
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>



