import router from './router';
import store from './store';
import NProgress from 'nprogress'; // Progress 进度条
import 'nprogress/nprogress.css'; // Progress 进度条样式
import { Message } from 'element-ui';
import { getToken } from '@/utils/auth'; // 验权

const whiteList = ['/login']; // 不重定向白名单
router.beforeEach((to, from, next) => {
  NProgress.start();

  if (getToken()) {
    if (to.path === '/login') {
      next({ path: '/' });
      NProgress.done(); // if current page is dashboard will not trigger	afterEach hook, so manually handle it
    } else {
      // 如果用户能访问的页面个数为0
      if (store.getters.views.length === 0) {
        store.dispatch('GetInfo').then(res => { // 拉取用户信息
          next();
        }).catch((err) => {
          store.dispatch('FedLogOut').then(() => {
            Message.error(err || 'Verification failed, please login again');
            next({ path: '/' });
          });
        });
      } else {
        var to_path = to.path;
        var user_views_path = [];
        for (const item of store.getters.views) {
          user_views_path.push(item.path);
        }
        user_views_path.push('/dashboard');

        // 如果在权限里面
        if (user_views_path.indexOf(to_path) !== -1) {
          console.log('in permission');
          next();
        } else {
          console.log('out permission');
          next({ path: '/' });
        }
        // next({ path: '/' });
      }
    }
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      next();
    } else {
      next('/login');
      NProgress.done();
    }
  }
});

router.afterEach(() => {
  NProgress.done(); // 结束Progress
});



