import request from '@/utils/request';

export function login(username, password) {
  return request({
    url: '/api/user/login',
    method: 'post',
    data: {
      username,
      password
    }
  });
}

export function getInfo(token) {
  return request({
    url: '/api/user/info',
    method: 'get',
    params: { token }
  });
}

export function logout() {
  return request({
    url: '/api/user/logout',
    method: 'post'
  });
}
export function getWhiteList() {
  return request({
    url: '/api/user/whiteList',
    method: 'get'
  });
}
export function deleteWhiteListItem(id) {
  return request({
    url: '/api/user/deleteWhiteListItem',
    method: 'get',
    params: {
      id
    }
  });
}
export function addWhiteListItem(whiteListItem) {
  return request({
    url: '/api/user/addWhiteListItem',
    method: 'post',
    data: {
      whiteListItem
    }
  });
}



