import Vue from 'vue';
import Router from 'vue-router';

// in development-env not use lazy-loading, because lazy-loading too many pages will cause webpack hot update too slow. so only in production use lazy-loading;
// detail: https://panjiachen.github.io/vue-element-admin-site/#/lazy-loading

Vue.use(Router);

/* Layout */
import Layout from '../views/layout/Layout';

/**
* hidden: true                   if `hidden:true` will not show in the sidebar(default is false)
* alwaysShow: true               if set true, will always show the root menu, whatever its child routes length
*                                if not set alwaysShow, only more than one route under the children
*                                it will becomes nested mode, otherwise not show the root menu
* redirect: noredirect           if `redirect:noredirect` will no redirct in the breadcrumb
* name:'router-name'             the name is used by <keep-alive> (must set!!!)
* meta : {
    title: 'title'               the name show in submenu and breadcrumb (recommend set)
    icon: 'svg-name'             the icon show in the sidebar,
  }
**/
export const constantRouterMap = [{
  path: '/login',
  component: () =>
            import('@/views/login/index'),
  hidden: true
},
{
  path: '/404',
  component: () =>
            import('@/views/404'),
  hidden: true
},

{
  path: '/',
  component: Layout,
  redirect: '/dashboard',
  name: 'Dashboard',
  hidden: true,
  children: [{
    path: 'dashboard',
    component: () =>
                import('@/views/dashboard/index')
  }]
},

{
  path: '/mail',
  component: Layout,
  redirect: '/mail/list',
  name: 'Mail',
  meta: { title: 'Mail', icon: 'example' },
  children: [{
    path: 'list',
    name: 'MailList',
    meta: { title: 'MailList', icon: 'table' },
    component: () =>
                    import('@/views/mail/index')
  },
  {
    path: 'send',
    name: 'SendMail',
    meta: { title: 'SendMail', icon: 'table' },
    component: () =>
                    import('@/views/mail/sendMail')
  }
  ]
},
{
  path: '/account',
  component: Layout,
  redirect: '/account/list',
  name: 'Account',
  meta: { title: 'Account', icon: 'example' },
  children: [{
    path: 'list',
    name: 'AccountList',
    meta: { title: 'AccountList', icon: 'table' },
    component: () =>
                    import('@/views/account/index')
  },
  {
    path: 'ban',
    name: 'Kick&Ban',
    meta: { title: 'Kick&Ban', icon: 'table' },
    component: () =>
                    import('@/views/account/kick_ban')
  }
  ]
},
{
  path: '/player',
  component: Layout,
  redirect: '/player/playerinfo',
  name: 'Player',
  meta: { title: 'Player', icon: 'example' },
  children: [{
    path: 'playerinfo',
    name: 'PlayerInfo',
    component: () =>
                import('@/views/player/playerInfo'),
    meta: { title: 'PlayerInfo', icon: 'example' }
  }]
},
{
  path: '/example',
  component: Layout,
  redirect: '/example/table',
  name: 'Example',
  meta: { title: 'Example', icon: 'example' },
  children: [{
    path: 'table',
    name: 'Table',
    component: () =>
                    import('@/views/table/index'),
    meta: { title: 'Table', icon: 'table' }
  },
  {
    path: 'tree',
    name: 'Tree',
    component: () =>
                    import('@/views/tree/index'),
    meta: { title: 'Tree', icon: 'tree' }
  }
  ]
},

  // {
  //   path: '/form',
  //   component: Layout,
  //   children: [{
  //     path: 'index',
  //     name: 'Form',
  //     component: () =>
  //                 import('@/views/form/index'),
  //     meta: { title: 'Form', icon: 'form' }
  //   }]
  // },
{
  path: '/activity',
  component: Layout,
  redirect: '/activity/switch',
  name: 'Activity',
  meta: { title: 'Activity', icon: 'example' },
  children: [{
    path: 'switch',
    name: 'Switch',
    component: () =>
                import('@/views/activity/switch'),
    meta: { title: 'Switch', icon: 'table' }
  },
  {
    path: 'notification',
    name: 'PushNotification',
    component: () =>
                import('@/views/activity/noti_page'),
    meta: { title: '推送', icon: 'table' }
  }]
},
{
  path: '/settings',
  redirect: '/settings/adminsmanage',
  component: Layout,
  name: 'settings',
  meta: { title: 'Settings', icon: 'example' },
  children: [{
    path: 'adminsmanage',
    component: () =>
                    import('@/views/settings/admins/index'),
    name: 'AdminsManage',
    meta: { title: 'AdminsM', icon: 'tree' } // 页面需要的权限
  },
  {
    path: 'adminedit',

    component: () =>
                    import('@/views/settings/admins/admin_edit'),
    name: 'AdminEdit',
    meta: { title: 'AdminEdit', icon: 'tree' }
  },
  {
    path: 'whitelist',
    component: () =>
                    import('@/views/settings/whitelist/whiteList'),
    name: 'WhiteList',
    meta: { title: 'WhiteList', icon: 'tree' }
  },
  {
    path: 'whitelistadd',
    component: () =>
                    import('@/views/settings/whitelist/whiteList_add'),
    name: 'WhiteList_add',
    meta: { title: 'WhiteList_add', icon: 'tree' }
  }

  ]
},

{ path: '*', redirect: '/404', hidden: true }
];

export default new Router({
  // mode: 'history', //后端支持可开
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
});
// 异步挂载的路由
// 动态需要根据权限加载的路由表
export const asyncRouterMap = [
  // {
  //   path: '/settings',
  //   redirect: '/settings/adminsmanage',
  //   component: Layout,
  //   name: 'settings',
  //   meta: { title: 'Settings', icon: 'example', roles: ['super_admin'] },
  //   children: [
  //     {
  //       path: 'adminsmanage',
  //       component: () => import('@/views/settings/admins/index'),
  //       name: 'AdminsManage',
  //       meta: { title: 'AdminsM', icon: 'tree', roles: ['super_admin'] } // 页面需要的权限
  //     },
  //     {
  //       path: 'serversmanage',
  //       component: () => import('@/views/settings/servers/index'),
  //       name: 'ServersManage',
  //       meta: { title: 'ServersM', icon: 'tree', roles: ['super_admin'] } // 页面需要的权限
  //     }
  //   ]
  // },

  { path: '*', redirect: '/404', hidden: true }
];



